import request from '@/utils/request'
export function addPaper(params) {
  return request({
    url: '/paper/add',
    method: 'get',
    params: params
  })
}

export function getPaperByTitle(t) {
  return request({
    url: '/paper/selectByTitle',
    method: 'get',
    params: {
      title: t
    }
  })
}

export function getAllPaper() {
  return request({
    url: '/paper/all',
    method: 'get',
  })
}

export function findByDataId(para) {
  return request({
    url: '/paper/findByDataId',
    method: 'get',
    params: para
  })
}

export function getPaperRanks() {
  return request({
    url: '/paper/getPaperRanks',
    method: 'get'

  })
}

export function addDataRefBatch(p) {
  return request({
    url: '/paper/addDataRefBatch',
    method: 'get',
    params: p

  })
}









