<template>
  <div style="margin-top: 50px">
    <!-- 添加新文献弹窗 -->
    <el-dialog :visible.sync="show.addNewPaper" width="90%">
      <el-form size="small" label-width="20%">
        <el-form-item label="文献名称:">
          <el-input v-model="v.paper.title" style="width:80%"></el-input>
        </el-form-item>
        <el-form-item label="作者:">
          <el-input v-model="v.paper.authors" style="width:80%"></el-input>
        </el-form-item>
        <el-form-item label="档次:">
          <el-select v-model="v.paper.rank">
            <el-option :label="r.content" :value="r.id" v-for="(r,ri) in list.paperRanks" :key="ri"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关键词:">
          <el-input v-model="v.paper.keywords" style="width:80%"></el-input>
        </el-form-item>
        <el-form-item label="发表日期:">
          <el-date-picker v-model="v.paper.publishDate" value-format="yyyy/MM/dd HH:mm:ss"></el-date-picker>
          <!-- <el-input v-model="v.paper.publishDate" style="width:80%"></el-input> -->
        </el-form-item>
        <el-form-item label="dio号码:">
          <el-input v-model="v.paper.dio" style="width:80%"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="addPaper">添加文献</el-button>
          <i class="el-icon-check"></i>
          <i class="el-icon-close"></i>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 添加已有文献引用 -->
    <el-form :model="value" ref="productRelationForm" label-width="120px" style="width: 680px; " size="small">
      <el-form-item label="相关文献：">
        <el-transfer style="display: inline-block;" filterable :filter-method="filterMethod" filter-placeholder="请输入文献名称" v-model="v.paperIds" :titles="['所有文献','添加到本数据集文献']" :data="list.papers"></el-transfer>
      </el-form-item>
      <el-form-item label>
        <el-row :gutter="20">
          <el-col :span="10" :offset="8">
            <el-button type="danger" round @click="reloadPaperRanks() ,show.addNewPaper=true">添加新文献</el-button>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item style="text-align: center">
        <el-button size="medium" @click="handlePrev" v-if="typ1==0">上一步</el-button>
        <el-button type="primary" size="medium" @click="handleFinishCommit">完成</el-button>
        <el-button type="primary" size="medium" @click="emitClose" v-if="typ1==1">关闭</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  addPaper,
  getPaperByTitle,
  getAllPaper,
  getPaperRanks
} from "@/api/paper";

export default {
  props: {
    value: Object,
    isEdit: {
      type: Boolean,
      default: false
    },
    // type属性。默认为0，标识新建数据集使用，1标识数据集详情中添加文献使用
    typ1: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      show: {
        addNewPaper: false
      },
      list: {
        papers: [],
        paperRanks: []
      },
      v: {
        // 待添加的文献信息
        paper: {
          title: "",
          authors: "",
          rank: "",
          keywords: "",
          dio: ""
        },
        // 与当前数据集相关的paperids
        paperIds: []
      }
    };
  },
  created() {
    this.reloadPapers();
    if (this.value.paperIds) {
      this.v.paperIds = this.value.paperIds;
    }
  },

  methods: {
    setPaperIds(ids) {
      this.v.paperIds = ids;
    },
    emitClose() {
      this.$emit("close");
    },
    // 刷新文章级别列表
    reloadPaperRanks() {
      getPaperRanks()
        .then(r => {
          if (r.code == 200) {
            this.list.paperRanks = r.data;
            if (this.list.paperRanks.length > 0)
              this.value.paperRank = this.list.paperRanks[0].id;
          }
        })
        .catch();
    },
    // 刷新相关文献列表
    reloadPapers() {
      getAllPaper()
        .then(r => {
          if (r.code == 200) {
            this.list.papers = r.data || [];
            this.list.papers.forEach(e => {
              e.key = e.id;
              e.label = e.title;
            });
          }
        })
        .catch();
    },
    addPaper() {
      let se = this;
      addPaper(se.v.paper)
        .then(r => {
          if (r.code == 200) {
            se.$message("添加成功");
            this.v.paper = {};
            this.show.addNewPaper = false;
            this.reloadPapers();
          } else {
            se.$message("添加失败");
          }
        })
        .catch(e => {
          se.$message("添加成功");
        });
    },

    filterMethod(query, item) {
      return item.title.indexOf(query) > -1;
    },

    handlePrev() {
      this.$emit("prevStep");
    },
    handleFinishCommit() {
      this.value.paperIds = this.v.paperIds.join(",");
      // this.value.paperIds = this.v.paperIds || [];

      this.$emit("finishCommit", this.isEdit);
    }
  }
};
</script>

<style>
.el-transfer-panel {
  width: 90%;
}

/* .el-transfer__buttons :nth-child(1){
  transform: rotate(90deg)
}
.el-transfer__buttons :nth-child(2){
  transform: rotate(-90deg)
} */
</style>



