<template>
  <div>
    <!-- 添加相关文献弹窗 -->
    <el-dialog :visible.sync="show.editPaperDesc" width="90%" :append-to-body="true" @opened="initEditor()" @closed="destroyEditor()" title="编辑参考文献">
      <div id="editorDiv">
        <div id="editor"></div>
      </div>
      <br>
      <el-divider></el-divider>
      <br>
      <el-row>
        <el-col :offset="9" :span="2">
          <el-button @click="updatePaperDesc()">确定</el-button>
        </el-col>
        <el-col :offset="1" :span="2">
          <el-button @click="show.editPaperDesc=false">取消</el-button>
        </el-col>
      </el-row>
    </el-dialog>

    <!-- <div v-show="show.editPaperDesc" width="90%;height:70%; box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)">
      <div id="editor"></div>
    </div> -->

    <!-- 头部 -->
    <flatGreyHead></flatGreyHead>

    <navBar cur="2"></navBar>

    <el-dialog :visible.sync="show.download" width="70%" title="下载">

      <!-- 如果是百度网盘 -->
      <div v-if="dataset.useNetDisk">
        <h3>这是一个存放在百度网盘上的文件，请点击下面的连接跳转到网盘地址</h3>
        <hr>
        <el-form label-width="100px">
          <el-form-item label="连接地址">
            <el-tag>
              <a :href="dataset.netDiskUrl" target="_blank">{{dataset.netDiskUrl}}</a>
            </el-tag>
          </el-form-item>
          <el-form-item label="提取密码">
            <el-tag>{{dataset.netDiskPassword}} </el-tag>
          </el-form-item>
        </el-form>
      </div>

      <!-- 如果不是百度网盘 -->
      <div v-else>
        <h3>这是一个存放在服务器上的文件，请点击下面的连接下载</h3>
        <hr>
        <el-tag>{{fileDownloadPath+"?fileCode="+dataset.fileCode}}</el-tag>
      </div>

    </el-dialog>

    <main class="ps-main">
      <div class="ps-product--detail pt-60">
        <div class="ps-container">

          <!-- 数据集标题部分 -->
          <el-page-header @back="goBack()" :content="dataset.title"></el-page-header>

          <el-row>
            <el-col :span="10" :offset="2">
              <h2 style="text-align: right; font-size: 70px;">{{dataset.title}}</h2>
            </el-col>
            <el-col :span="6">
              <h3 style="text-align: left; font-size: 40px; font-style: italic">{{dataset.subTitle}}</h3>
            </el-col>

          </el-row>

          <el-divider></el-divider>
          <br>
          <br>
          <br>

          <el-row>
            <!-- 数据照片部分 -->
            <el-col :span="10" :offset="4">
              <img v-if="dataset.logo" :src="fileDownloadPath+'?fileCode='+ dataset.logo" style="height:100%; width:100%">
              <img v-else src="../assets/images/数据集成2.png" alt style="height:100%; width:100%">
            </el-col>

            <!-- star部分 -->
            <el-col :span="5" :offset="1">
              <el-form size='mini' style="margin-top:130px; margin-left:20px">
                <!-- 数据评分 -->
                <el-form-item label="数据评分">
                  <el-rate v-model="rateScore"></el-rate>
                </el-form-item>
                <!-- 收藏次数 -->
                <el-form-item label="收藏次数">
                  {{collectTimes}}
                </el-form-item>
                <!-- 下载指数 -->
                <el-form-item label="下载次数">
                  {{dataset.downloadTimes}}
                </el-form-item>
                <!-- 数据大小 -->
                <el-form-item label="数据大小">
                  {{dataset.dataSize}}
                </el-form-item>
              </el-form>

              <el-divider></el-divider>

            </el-col>
          </el-row>

          <!-- 数据按钮 -->
          <el-row>
            <el-col :span="7" :offset="15">
              <!-- 操作按钮 -->
              <el-button-group>
                <el-button class="el-icon-star-on" round="" size="mini" @click="addCollect(dataset)">收藏</el-button>
                <el-button class="el-icon-download" round="" @click="download(dataset)" size="mini">下载</el-button>
                <el-button class="el-icon-s-promotion" round="" size="mini" @click="share(dataset)">分享</el-button>
              </el-button-group>
            </el-col>
            <el-col :span="6">
            </el-col>
          </el-row>

          <div class="row">
            <div class="col-lg-10 col-md-12 col-lg-offset-1">

              <div class="ps-product__info">

              </div>
              <div class="clearfix"></div>
              <div class="ps-product__content mt-50">
                <ul class="tab-list" role="tablist">
                  <li :class="{active:show.detail_tab==1}">
                    <a aria-controls="tab_01" @click="show.detail_tab=1">数据描述</a>
                  </li>
                  <li :class="{active:show.detail_tab==2}">
                    <a aria-controls="tab_02" @click="show.detail_tab=2">数据表格</a>
                  </li>
                  <li :class="{active:show.detail_tab==3}">
                    <a aria-controls="tab_03" @click="show.detail_tab=3,reload_comments()">评论</a>
                  </li>
                  <li :class="{active:show.detail_tab==4}">
                    <a aria-controls="tab_04" @click="show.detail_tab=4">相关文献</a>
                  </li>
                </ul>
              </div>
              <div class="tab-content mb-60" style="padding-left: 150px; padding-right: 150px; min-height:600px">
                <div class="tab-pane active" v-show="show.detail_tab==1">
                  <div v-html="dataset.dataDesc" v-if="dataset.dataDesc"></div>
                  <div v-else>偷懒没写...</div>
                </div>
                <div class="tab-pane active" v-show="show.detail_tab==2">
                  <div v-html="dataset.dataTable" v-if="dataset.dataTable"></div>
                  <div v-else>偷懒没写...</div>
                </div>

                <!-- 评论 -->

                <div class="tab-pane active" v-show="show.detail_tab==3">

                  <div class="ps-product__tags" style="padding-top: 15px; margin-left:20%; margin-bottom:20px">
                    <div class="form-group">
                      <input class="form-control" type="text" placeholder v-model.trim="comment">
                      <button class="ps-btn ps-btn--sm" @click="addComment()">吐槽</button>
                    </div>
                  </div>

                  <div v-if="list.comments.length > 0">
                    <el-row v-for="(c,ci) in list.comments" :key="ci">

                      <el-col :offset="1" :span="18">
                        <p>
                          {{c.content}}
                        </p>
                      </el-col>

                      <el-col :span="2">
                        <i class="el-icon-delete-solid" @click="delComment(c.id)"></i>
                      </el-col>
                      <el-divider></el-divider>
                    </el-row>

                  </div>
                  <div v-else style="text-align: center;">吐槽都去哪了?</div>

                  
                </div>

                <!-- 相关文献 -->
                <div class="tab-pane active" v-show="show.detail_tab==4">
                  <el-button type="primary" round icon="el-icon-edit" size="mini" @click="editPaperDesc('show')">编辑参考文献</el-button>

                  <div v-if="dataset.paperDesc" v-html="dataset.paperDesc"></div>
                  <div v-else style="padding-top: 20px">还没有文献敢使用该数据集~</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>


<script>
const defaultListQuery = {
  keyword: null,
  pageNum: 1,
  pageSize: 5,
  publishStatus: null,
  verifyStatus: null,
  productSn: null,
  productCategoryId: null,
  brandId: null
};

import { dataDetail, updatePaperDesc } from "@/api/datasets";
import tinymce from "@/components/Tinymce/index";
import flatGreyHead from "@/components/header/flatGreyHead";
import datasetPapers from "@/pages/create/components/datasetPapers";
import navBar from "@/components/header/navBar";
import { Loading } from "element-ui";
import { addComment, findCommentsByTarget, delComment } from "@/api/comments";

import { addCollect } from "@/api/collect";

export default {
  components: {
    flatGreyHead,
    datasetPapers,
    navBar,
    tinymce
  },
  created() {
    this.dataId = this.$route.query.id;
    this.reloadDataDetail();
  },
  methods: {
    // 更新参考文献信息
    updatePaperDesc() {
      updatePaperDesc({
        datasetId: this.dataId,
        paperDesc: this.editor.txt.html()
      }).then(r => {
        if (r.code == 200) {
          this.$message({
            message: "更新成功",
            type: "success"
          });

          // 关闭弹窗和编辑器
          this.show.editPaperDesc=false
          this.destroyEditor()
          this.reloadDataDetail()
        } else {
          this.$message({
            message: "更新失败",
            type: "error"
          });
        }
      });
    },
    // 初始化编辑器
    initEditor() {
      var E = window.wangEditor;
      this.editor = new E("#editor");
      this.editor.create();
      this.editor.txt.html(this.dataset.paperDesc || "");
    },

    // 销毁编辑器
    destroyEditor() {
      document.getElementById(
        "editorDiv"
      ).innerHTML = `<div id='editor'></div>`;
    },
    handleChange(v) {
      console.log(v);
    },
    // 返回数据列表页面
    goBack() {
      this.$router.push("/datas");
    },
    // 添加收藏关系
    addCollect() {
      addCollect({
        targetId: this.v.id,
        type: "DATA_COLLECT"
      });
    },
    addPapers() {
      addDataRefBatch({
        data_id: this.v.id,
        paperIds: this.v.paperModel.paperIds
      }).then(r => {
        if (r.code == 200) {
          this.$message({
            message: "添加成功",
            type: "success"
          });
        }
      });
    },
    editPaperDesc(v) {
      if (v == "show") {
        this.show.editPaperDesc = true;
        // var E=window.wangEditor
        // var editor= new E("#editor")
        // editor.create()
      }
    },

    // 删除评论
    delComment(id) {
      delDataCommentsById({ id: id }).then(r => {
        if (r.code == 200) {
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.reload_comments();
        }
      });
    },
    // 添加评论
    addComment() {
      addComment({
        content: this.comment,
        targetId: this.dataId
      }).then(r => {
        if (r.code == 200) {
          this.$message({
            message: "添加成功",
            type: "success"
          });
          this.reload_comments();
          this.comment = "";
        }
      });
    },
    // 刷新评论
    reload_comments() {
      findCommentsByTarget({
        targetId: this.dataId
      }).then(r => {
        if (r.code == 200) {
          r.data = r.data || [];
          r.data.forEach(element => {
            element.replys = [];
          });
          this.list.comments = r.data;
        }
      });
    },

    // 获取一个评论的回复
    reloadReply(c) {
      findCommentsByTarget({
        targetId: c.id
      }).then(r => {
        if (r.code == 200) {
          r.replys.push(r.data);
        }
      });
    },

    // 数据集下载
    download(d) {
      this.show.download = true;
    },
    //   刷新数据信息
    reloadDataDetail() {
      let that = this;

      dataDetail({ id: that.dataId })
        .then(r => {
          if (r.code == 200) {
            that.dataset = r.data || {};
            that.dataDesc = decodeURIComponent(that.dataDesc);
            that.dataTable = decodeURIComponent(that.dataTable);
          }
        })
        .catch(e => {});
    }
  },
  data() {
    return {
      // 编辑器对应实体对象
      editor: "",
      // 编辑器输入的新的参考文献信息
      paperDesc: "",
      showReplyComments: [],
      v: {},
      // 添加的评论
      comment: "",
      // 数据集属性
      dataset: {},

      // 打分分数
      rateScore: 0,
      // 收藏次数
      collectTimes: 0,
      dataId: "",

      loadings: {},

      show: {
        //   1/2/3/4
        detail_tab: "1",
        editPaperDesc: false,
        download: false
      },
      title: "商品名称",
      list: {
        papers: [],
        comments: []
      }
    };
  }
};
</script>



<style scoped>
@import url(./../assets/css/style2.css);
@import url(./../assets/plugins/bootstrap/dist/css/bootstrap.min.css);
@import url(./../assets/plugins/owl-carousel/assets/owl.carousel.css);
@import url(./../assets/plugins/jquery-bar-rating/dist/themes/fontawesome-stars.css);
@import url(./../assets/plugins/slick/slick/slick.css);
@import url(./../assets/plugins/bootstrap-select/dist/css/bootstrap-select.min.css);
@import url(./../assets/plugins/Magnific-Popup/dist/magnific-popup.css);
@import url(./../assets/plugins/jquery-ui/jquery-ui.min.css);
@import url(./../assets/plugins/revolution/css/settings.css);
@import url(./../assets/plugins/revolution/css/layers.css);
@import url(./../assets/plugins/revolution/css/navigation.css);

.fav-item {
  width: 200px;
  height: 200px;
  border-style: dashed;
  float: left;
  margin: 10px;
}

.fav-item-tag {
  background: red;
  width: 88px;
  height: 44px;
  font-size: 36px;
  margin-top: 18px;
  margin-left: 30px;
  border-radius: 10px;
}

.mainPic {
  width: 200px;
  height: 200px;
}
</style>



